import uniRate from '../../components/uni-ui/uni-rate/uni-rate.vue'
import actorBox from '../../components/detail/actorBox/actorBox.vue'
export default {
	name: "",
	components: {
		actorBox,
		uniRate

	},
	props: {},
	data() {
		return {
			plotPics: null,
			details: null,
			directors: null,
			actors: null,
			score: 0
		}
	},
	methods: {
		getData(id) {
			uni.showLoading({
				title: "加载中..."
			})
			uni.request({
				url: this.$api(`/search/trailer/${id}/`),
				method: 'POST',
				success: (res) => {
					this.details = res.data.data
					this.score = res.data.data.score
					this.plotPics = JSON.parse(res.data.data.plotPics)
				},
				fail() {},
				complete() {
					uni.hideLoading()
				}
			})
		},
		getActor(id, role) {
			uni.showLoading({
				title: "加载中..."
			})
			uni.request({
				url: this.$api(`/search/staff/${id}/${role}`),
				method: 'POST',
				success: (res) => {
					role === 1 ? this.directors = res.data.data : this.actors = res.data.data
				},
				fail() {},
				complete() {
					uni.hideLoading()
				}
			})
		}
	},
	mounted() {

	},
	onLoad(options) {
		// options.id = 'marvel-1011'
		this.getData(options.id)
		this.getActor(options.id, 1)
		this.getActor(options.id, 2)
	},
	onShareAppMessage(){
		console.log("点击了分享");
	},
	filters: {

	},
	computed: {

	},
	watch: {

	},
	directives: {

	}
}
